.App {
  text-align: center;
  background-color: #1f2937;
  color: #111827;
  color:#222222;
  background-color: rgb(231, 231, 231);
}
.formBtnContainer{
  max-width:88%;
}
.buttonsContainer{
  padding-left: 3.2rem;
}
/* Base switch styling */
.form-check-input {
  border-color: #26a14b; /* Green border color when unchecked */
}

/* Custom switch color when checked */
.form-check-input:checked {
  background-color: #26a14b;
  border-color: #26a14b;
}

/* Custom slider handle */
.form-check-input:checked ~ .form-check-label::before {
  background-color: #26a14b; /* Green slider handle color when checked */
}

/* Slider handle appearance */
.form-check-label::before {
  background-color: #ffffff; /* Default slider handle color */
  border: 2px solid #26a14b; /* Optional: border around the handle */
}

/* Green shadow on focus */
.form-check-input:focus {
  box-shadow: 0 0 0 0.2rem rgba(38, 161, 75, 0.25);
}

/* Slider handle transition for smooth effect */
.form-check-input:checked ~ .form-check-label::before {
  transform: translateX(1rem); /* Adjust based on your switch size */
}

/* Optional: Transition effect for slider */
.form-check-label::before {
  transition: background-color 0.2s ease, transform 0.2s ease;
}
